  background-color: white;
  box-shadow: 2px 2px 7px 2px #0000003b;

  min-height: 25px;
  border-radius: 5px;
}

.radio-group_tooltip-name {
  height: 25px;
  background-color: var(--c-violet);
  box-sizing: inherit;
}
.radio-group_tooltip-description {

}
